const app = getApp();
const common = require('../../../publicjs/common.js');
const utils = require('../../../utils/utils.js');
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import uniIcons from "@/components/uni-icons/uni-icons.vue";

export default {
	components: {
		uniNavBar,
		uniIcons,
	},
	data() {
		return {
			avatar: 'https://cdn.jsdelivr.net/gh/foxhuli229/cdn@1.4/images/z_avater.png', //头像
			nickname: '未毕之毕_', //昵称
			sex: 1, //性别
			date: utils.dateformat({ format: true }), //生日选择日期
			startDate: utils.dateformat('start'), //生日开始日期
			endDate: utils.dateformat('end'), //生日结束日期
			tel: '', //手机
			email: '',//邮箱
			school: '', //院校
			department: '', //系
			major: '', //专业，
			identity: '', //身份证
			placeholderClass: {
				textAlign: 'right',
				fontSize: '30rpx'
			},
			homepagesurl: '/pages/userCentre/baseinfo/baseinfo', //返回跳转的页面
			pagesurl: ''
		};
	},
	onLoad(option) {
		if(option !== "") {
			let param = JSON.parse(option.item);
			this.pagesurl = param.pagesurl;
		}
	},
	onShow() {
		
	},
	mounted() {
		
	},
	watch: {
		pagesurl(newVal, val) {
			//需要跳转返回的页面
			this.pagesurl = newVal;
			return newVal;
		}
	},
	methods: {
		//打开手机的图库, 头像上传
		chooseImage(event) {
			uni.chooseImage({
				count: 1,
				sourceType: 'album',
				success: res => {
					console.log(res);
					this.avatar = res.tempFilePaths[0];
				},
				fail: err => {

					uni.getSetting({
						success: res => {
							let authStatus = res.authSetting['scope.album'] && res.authSetting['scope.camera'];
							if (!authStatus) {
								uni.showModal({
									title: '授权失败',
									content: '需要从您的相册获取图片，请在设置界面打开相关权限',
									success: res => {
										if (res.confirm) {
											uni.openSetting();
										}
									}
								});
							}
						}
					});

				}
			});
		},

		navskip(event, title, value) {
			console.log(this[value]);
			let param = {
				pagesurl: "/pages/userCentre/baseinfo/baseinfo",
				leftIcon: "arrowleft",
				leftText: "个人信息",
				backgroundColor: "#4C6FF3",
				circular: "true",
				title,
				"avatar": this.avatar,
				"value": this[value]
			}
			param = JSON.stringify(param)
			uni.navigateTo({
				url: "../navBackInfoVerify/navBackInfoVerify?item="+ param,
			})
		},

		radioChange(event) {
			this.sex = event.target.value;
		},
		//出生日期
		bindDateChange: function(e) {
			this.date = e.detail.value;
		},
		saveBseInfo() {
			
		}
	}
};
